import React, { Component } from 'react';
import Input from './Input';
import './QuizForm.css';

class QuizForm extends Component {

  render() {
    return (
      <div className="QuizForm">
        <h4 className="ui header">{this.props.header}</h4>
        <Input label="" title="quiz" id={this.props.id} name="title" required={true} status={this.props.title ? "" : "error"} onChange={this.props.onChange} placeholder="" default={this.props.title} />
        <Input label="" title="quiz" id={this.props.id} name="description" onChange={this.props.onChange} placeholder="" default={this.props.description} />
        <Input label="" title="quiz" disabled={this.props.lockID} id={this.props.id} name="id" required={true} status={this.props.id ? "" : "error"} onChange={this.props.onChange} placeholder="" default={this.props.id} />
      </div>
    );

  }

}

export default QuizForm;



